								</el-icon>
								<el-icon @click="countTypeClick('closegangweishenqingCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddengangweishenqingCountType">
							<div class="count_title">岗位申请总数</div>
							<div class="count_num">{{gangweishenqingCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('mianshijieguo','首页总数')">
				<el-card v-show="countTypeList.closemianshijieguoCountType" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								面试结果
							</div>
							<div class="card_head_right">
								<el-icon @click="countTypeClick('hiddenmianshijieguoCountType')" class="showIcons"
									:class="countTypeList.hiddenmianshijieguoCountType?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="countTypeClick('closemianshijieguoCountType')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
							
						</div>
					</template>
					<el-collapse-transition>
						<div class="count_item" v-show="countTypeList.hiddenmianshijieguoCountType">
							<div class="count_title">面试结果总数</div>
							<div class="count_num">{{mianshijieguoCount}}</div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
		<div class="card_list">
			<el-collapse-transition v-if="btnAuth('zhaopinxinxi','首页统计')">
				<el-card v-show="cardTypeList.closezhaopinxinxiChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								招聘信息
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenzhaopinxinxiChartType1')" class="showIcons"
									:class="cardTypeList.hiddenzhaopinxinxiChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closezhaopinxinxiChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenzhaopinxinxiChartType1">
							<div id="zhaopinxinxifabushijianEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('gangweishenqing','首页统计')">
				<el-card v-show="cardTypeList.closegangweishenqingChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								岗位申请
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddengangweishenqingChartType1')" class="showIcons"
									:class="cardTypeList.hiddengangweishenqingChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closegangweishenqingChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddengangweishenqingChartType1">
							<div id="gangweishenqinggangweileixingEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
			<el-collapse-transition v-if="btnAuth('mianshijieguo','首页统计')">
				<el-card v-show="cardTypeList.closemianshijieguoChartType1" class="card_view">
					<template #header>
						<div class="index_card_head">
							<div class="card_head_title">
								面试结果
							</div>
							<div class="card_head_right">
								<el-icon @click="cardTypeClick('hiddenmianshijieguoChartType1')" class="showIcons"
									:class="cardTypeList.hiddenmianshijieguoChartType1?'showIcons1':''">
									<ArrowUpBold />
								</el-icon>
								<el-icon @click="cardTypeClick('closemianshijieguoChartType1')" class="closeIcons">
									<CloseBold />
								</el-icon>
							</div>
						</div>
					</template>
					<el-collapse-transition>
						<div class="card_item" v-show="cardTypeList.hiddenmianshijieguoChartType1">
							<div id="mianshijieguomianshijieguoEchart1" style="width: 100%;height: 400px;"></div>
						</div>
					</el-collapse-transition>
				</el-card>
			</el-collapse-transition>
		</div>
	</div>
</template>

<script setup>
	import {
		inject,
		nextTick,
		ref,
		getCurrentInstance
	} from 'vue';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const projectName = context.$project.projectName
	const countTypeList = ref({
	})
	const getCountList=()=>{
		countTypeList.value.closezhaopinxinxiCountType = true
		countTypeList.value.hiddenzhaopinxinxiCountType = true
		if(btnAuth('zhaopinxinxi','首页总数')){
			getzhaopinxinxiCount()
		}
		countTypeList.value.closegangweishenqingCountType = true
		countTypeList.value.hiddengangweishenqingCountType = true
		if(btnAuth('gangweishenqing','首页总数')){
			getgangweishenqingCount()
		}
		countTypeList.value.closemianshijieguoCountType = true
		countTypeList.value.hiddenmianshijieguoCountType = true
		if(btnAuth('mianshijieguo','首页总数')){
			getmianshijieguoCount()
		}
	}
	const zhaopinxinxiCount = ref(0)
	const getzhaopinxinxiCount = () => {
		context?.$http({
			url:'zhaopinxinxi/count',
			method: 'get'
		}).then(res=>{
			zhaopinxinxiCount.value = res.data.data
		})
	}
	const gangweishenqingCount = ref(0)
	const getgangweishenqingCount = () => {
		context?.$http({
			url:'gangweishenqing/count',
			method: 'get'
		}).then(res=>{
			gangweishenqingCount.value = res.data.data
		})
	}
	const mianshijieguoCount = ref(0)
	const getmianshijieguoCount = () => {
		context?.$http({
			url:'mianshijieguo/count',
			method: 'get'
		}).then(res=>{
			mianshijieguoCount.value = res.data.data
		})
	}
	const countTypeClick = (e) => {
		countTypeList.value[e] = !countTypeList.value[e]
	}
	const init=()=>{
		getCountList()
		getCardList()
	}
	//权限验证
	const btnAuth = (e,a)=>{
		return context?.$toolUtil.isAuth(e,a)
	}
	let echarts = inject("echarts")
	const cardTypeClick = (e) =>{
		cardTypeList.value[e] = !cardTypeList.value[e]
		setTimeout(()=>{
			getCardList()
		},1000)
	}
	const cardTypeList = ref({
		closezhaopinxinxiChartType1: true,
		hiddenzhaopinxinxiChartType1: true,
		closegangweishenqingChartType1: true,
		hiddengangweishenqingChartType1: true,
		closemianshijieguoChartType1: true,
		hiddenmianshijieguoChartType1: true,
	})
	const getCardList = () => {
		if(btnAuth('zhaopinxinxi','首页统计')){
			getzhaopinxinxiChart1()
		}
		if(btnAuth('gangweishenqing','首页统计')){
			getgangweishenqingChart1()
		}
		if(btnAuth('mianshijieguo','首页统计')){
			getmianshijieguoChart1()
		}
	}
	const getzhaopinxinxiChart1 = () => {
		nextTick(()=>{
			var fabushijianEchart1 = echarts.init(document.getElementById("zhaopinxinxifabushijianEchart1"),'macarons');
			context?.$http({
				url: "zhaopinxinxi/group/fabushijian",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].fabushijian);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].fabushijian
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '岗位统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				fabushijianEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				fabushijianEchart1.setOption(option);
				//根据窗口的大小变动图表
				fabushijianEchart1.resize();
			})
		})
	}
	const getgangweishenqingChart1 = () => {
		nextTick(()=>{
			var gangweileixingEchart1 = echarts.init(document.getElementById("gangweishenqinggangweileixingEchart1"),'macarons');
			context?.$http({
				url: "gangweishenqing/group/gangweileixing",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].gangweileixing);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].gangweileixing
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '岗位申请类型统计',
                        left: 'center'
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left'
                    },
                    tooltip: {
						trigger: 'item',
						formatter: '{b} : {c} ({d}%)'
                    },
                    series: [
                        {
                            type: 'pie',
                            radius: '55%',
                            center: ['50%', '60%'],
                            data: pArray,
                            emphasis: {
                                itemStyle: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
									shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                };
				gangweileixingEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				gangweileixingEchart1.setOption(option);
				//根据窗口的大小变动图表
				gangweileixingEchart1.resize();
			})
		})
	}
	const getmianshijieguoChart1 = () => {
		nextTick(()=>{
			var mianshijieguoEchart1 = echarts.init(document.getElementById("mianshijieguomianshijieguoEchart1"),'macarons');
			context?.$http({
				url: "mianshijieguo/group/mianshijieguo",
				method: "get",
			}).then(obj=>{
				let res = obj.data.data
				let xAxis = [];
				let yAxis = [];
				let pArray = []
				for(let i=0;i<res.length;i++){
				    xAxis.push(res[i].mianshijieguo);
				    yAxis.push(parseFloat((res[i].total)));
				    pArray.push({
				        value: parseFloat((res[i].total)),
				        name: res[i].mianshijieguo
				    })
				}
				var option = {};
                option = {
                    title: {
                        text: '面试结果统计',
                        left: 'center'
                    },
                    tooltip: {
                      trigger: 'item',
                      formatter: '{b} : {c}'
                    },
                    xAxis: {
                        type: 'category',
                        data: xAxis,
                        axisLabel: {
                            rotate: 40
                        }
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: yAxis,
                        type: 'bar'
                    }]
                };
				mianshijieguoEchart1.clear()
				// 使用刚指定的配置项和数据显示图表。
				mianshijieguoEchart1.setOption(option);
				//根据窗口的大小变动图表
				mianshijieguoEchart1.resize();
			})
		})
	}
	init()
</script>
<style lang="scss">
	.projectTitle{
		padding: 20px 0;
		margin: 20px 0 20px;
		color: #4F1904;
		font-weight: bold;
		display: flex;
		width: 100%;
		font-size: 22px;
		justify-content: center;
		align-items: center;
		height: auto;
	}

	.showIcons {
		transition: transform 0.3s;
		margin-right: 10px;
	}

	.showIcons1 {
		transform: rotate(-180deg);
	}
	
	// 总数盒子
	.count_list{
		padding: 0 0 20px;
		grid-column-gap: 20px;
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		// 总数card
		.card_view {
			border: 1px solid #ddd;
			box-shadow: 0px 0px 0px rgba(0,0,0,.12);
			padding-top: 60px;
			flex: 1;
			background: linear-gradient(90deg,#FFFAF2, #FDE9CB);
			width: 100%;
			position: relative;
			box-sizing: border-box;
			height: auto;
			// card头部
			.el-card__header {
				border-radius: 20px 0 0 20px;
				padding: 0 20px;
				top: 20px;
				background: #EED6B4;
				line-height: 40px;
				position: absolute;
				right: 0;
				border-bottom: none;
				// 头部盒子
				.index_card_head {
					display: flex;
					width: 100%;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #313131;
						font-size: 14px;
						margin-right: 40px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon {
							cursor: pointer;
							color: #313131;
							font-weight: 700;
							font-size: 14px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.count_item{
					padding: 30px;
					text-align: center;
					// 总数标题
					.count_title{
						color: #9E9E9E;
						font-size: 18px;
						line-height: 2;
						text-align: left;
					}
					// 总数数字
					.count_num{
						color: #E7B668;
						font-weight: 700;
						font-size: 64px;
						line-height: 1;
						text-align: left;
					}
				}
			}
		}
	}
	// 首页盒子
	.home_view {
		padding: 0px 30px;
		margin: 0;
		background: url(http://clfile.zggen.cn/20240203/480e76b8afa24188a2f7a3a171f56e7d.png) no-repeat center top / 100% 100% !important;
		width: 100%;
		min-height: 100vh;
		height: auto;
	}
	// 统计图盒子
	.card_list {
		padding: 0 0 20px;
		display: flex;
		width: 100%;
		justify-content: space-between;
		align-items: flex-start;
		flex-wrap: wrap;
		// 统计图card
		.card_view {
			border: 1px solid #ddd;
			box-shadow: 0px 0px 0px rgba(0,0,0,.12);
			padding-top: 60px;
			margin: 0 1% 30px;
			width: 48%;
			position: relative;
			box-sizing: border-box;
			height: auto;
			// 头部
			.el-card__header {
				border-radius: 20px 0px 0px 20px;
				padding: 0px 20px;
				top: 20px;
				background: rgb(238, 214, 180);
				line-height: 40px;
				position: absolute;
				right: 0;
				border-bottom: none;
				// 头部盒子
				.index_card_head {
					display: flex;
					justify-content: space-between;
					align-items: center;
					// 标题
					.card_head_title {
						color: #313131;
						font-size: 14px;
					}
					// 按钮盒子
					.card_head_right {
						display: flex;
						align-items: center;
						// 按钮
						.el-icon{
							cursor: pointer;
							color: #313131;
							font-weight: 700;
							font-size: 14px;
						}
					}
				}
			}
			// body
			.el-card__body {
				padding: 0;
				// body盒子
				.card_item{
					padding: 30px;
					text-align: center;
				}
			}
		}
	}
</style>
